    <el-card :body-style="{ padding: '0px' }">
      <div
        class="card"
        @mouseenter="show(true)"
        @mouseleave="show(false)"
        @click="show(true)"
        v-Clickoutside="handleClickOutside"
      >
        <div class="image-box">
          <transition
            name="el-fade-in-linear"
          >
            <el-image
              v-if="!showChat"
              :src="data.imageUrl"
              class="outside-image"
            ></el-image>
          </transition>
            <div
              class="chat"
              v-if="showChat"
            >
              <template v-if="data.type == 'chat'">
                  <div class="title">
                    {{ data.pName }}
                    <span>
                      <el-button
                        class="more"
                        v-if="data.display == 'true'"
                        size="medium"
                        type="primary" plain
                        @click="goURL(`https://housetube.tw/${data.pid}`)"
                        round>
                        建案詳情
                      </el-button>
                    </span>
                  </div>
                  <template v-if="itemSize == 'big'">
                    <div class="message">
                      那邊如何？
                    </div><br>
                    <div class="message">
                      還在蓋的時候去問過了，在南科北路和興農路交叉口附近
                    </div><br>
                  </template>
                  <div class="message">
                    還有更多內幕消息？
                  </div><br>
                  <div class="message">
                    店面開價1000萬聽說賣完了，裡面還有透天和大樓，價格798萬的只有一間，其他都8起跳了，傳統透天車庫。
                  </div><br>
                  <div class="message">
                    快銷售光了！
                  </div><br>
                  <!-- <div class="chat-last">
                    最新消息，請至房地王聊天室
                  </div> -->
              </template>
              <template v-if="data.type === 'ad'">
                <el-image
                  :src="data.adImage"
                ></el-image>
              </template>
            </div>
        </div>
        <transition
          name="el-fade-in"
          mode="out-in"
        >
          <div v-if="!showChat" class="outside-bottom">
            <div class="info">
              <div class="pname">
                {{ data.pName }}
              </div>
              <div class="icon-line">
                <template v-if="data.type == 'chat'">
                  <i class="el-icon-user" />{{ data.collect }}
                  <i class="el-icon-chat-square" />8
                  <i class="el-icon-time" />3分鐘前
                </template>
                <template v-if="data.type === 'ad'">
                  <span class="axd-text">
                    精選！超高討論度新建案
                  </span>
                </template>
              </div>
            </div>
          </div>
          <div v-else>
            <div class="overlay" />
              <template v-if="data.type == 'chat'">
                <div class="live">
                  線上{{ data.collect }}人
                </div>
              </template>
              <template v-if="data.type === 'ad'">
                <div class="live" style="background-color:#ffffff00;">
                  廣告
                </div>
              </template>
            <div class="chat-enter">
              <template v-if="data.type == 'chat'">
                <el-button
                  type="primary"
                  round
                >
                  進入聊天室
                </el-button>
                <el-button round>
                  App下載
                </el-button>
              </template>
              <template v-if="data.type === 'ad'">
                <el-button
                  type="danger"
                  round
                >
                  立即暸解
                </el-button>
              </template>
            </div>
          </div>
        </transition>
      </div>
    </el-card>
  </div>
</template>

<script>
import Clickoutside from 'element-ui/src/utils/clickoutside';

export default {
  directives: { Clickoutside },
  name: 'ChatItem',
  props: {
    data: {
      type: Object,
    },
  },
  data() {
    return {
      showChat: false,
      itemSize: 'normal',
      mobile: false,
    };
  },
  created() {
    if (document.body.clientWidth <= 992) {
      this.mobile = true;
    }
    if (this.mobile) {
      document.addEventListener('scroll', this.handleScrollOpen);
      document.addEventListener('scroll', this.handleScrollClose);
    }
  },
  destroyed() {
    if (this.mobile) {
      document.removeEventListener('scroll', this.handleScrollOpen);
      document.removeEventListener('scroll', this.handleScrollClose);
    }
  },
  mounted () {
    this.$nextTick(() => {
      if (this.$refs.chatItem.clientWidth > 460) {
        this.itemSize = 'big';
      }
    })
  },
  methods: {
    goURL(url) {
       window.open(url);
    },
    show(v) {
      this.showChat = v;
    },
    handleClickOutside(){
      this.show(false);
    },
    handleScrollOpen() {
      const el = this.$refs.chatItem;
      if(elementInViewport(el, 'open')) {
        document.removeEventListener('scroll', this.handleScrollOpen);
        this.show(true);
      }
    },
    handleScrollClose() {
      const el = this.$refs.chatItem;
      if(elementInViewport(el, 'close')) {
        document.removeEventListener('scroll', this.handleScrollClose);
        this.show(false);
      }
    }
  },
};

function elementInViewport(el, type) {
  var top = el.offsetTop;
  var height = el.offsetHeight;

  while(el.offsetParent) {
    el = el.offsetParent;
    top += el.offsetTop;
  }

  if (type === 'open') {
    return (
      top >= window.pageYOffset &&
      (top + height) <= (window.pageYOffset + window.innerHeight)
    );
  } else {
    return (
      top >= window.pageYOffset &&
      (top + window.innerHeight*0.5 + height) <= (window.pageYOffset + window.innerHeight)
    );
  }
}
</script>

