      ],

      chatList: {},
      dataChatList: dataChatList,
    };
  },
  created: function() {
    if (document.body.clientWidth <= 992) {
      this.mobile = true;
    }

    for (let i = 0; i < 2; i++) {
      this.dataChatList.hot = [...this.dataChatList.hot, ...this.dataChatList.hot];
      this.dataChatList.new = [...this.dataChatList.new, ...this.dataChatList.new];
    }
    this.tagsHandleClick();
  },
  methods: {
    tagsHandleClick() {
      this.chatList = this.dataChatList[this.listType];
    },
  },
};
