<template>
  <div class="header-wrapper"
  :style="mobileShow?'bottom: 0;':''">
    <div
      v-if="mobile"
      class="moblie-menu"
    >
      <el-row :gutter="0">
        <el-col :span="3" >
          <div @click="mobileShow = !mobileShow;">
            <svg class="fas fa-bars" />
          </div>
        </el-col>
        <el-col :span="18">
          <div style="text-align: center;">
            房地王聊天首頁
          </div>
        </el-col>
      </el-row>
    </div>
    <transition name="el-fade-in-linear">
      <el-menu
        v-if="!mobile || mobileShow"
        :default-active="activeIndex"
        :mode="mobile?'':'horizontal'"
      >
        <el-menu-item index="99" @click="mobileShow = !mobileShow;">
          房地王聊天首頁
        </el-menu-item>
        <template v-for="item ,index in data">
          <el-menu-item
            v-if="!item.layer"
            :index="`${item.id}`"
            @click="goURL(item.link)"
          >
            <span slot="title">
              {{ item.title }}
            </span>
          </el-menu-item>
          <el-submenu
            v-if="item.layer"
            :index="`${item.id}`"
          >
            <template slot="title">
              {{ item.title }}
            </template>
            <el-menu-item
              v-for="one ,index in item.layer"
              :index="`${item.id}-${one.id}`"
              @click="goURL(one.link)"
            >
              {{ one.title }}
            </el-menu-item>
          </el-submenu>
        </template>
      </el-menu>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Header',
  props: {
    data: {
      type: Array,
    },
  },
  data() {
    return {
      activeIndex: '99',
      mobile: false,
      mobileShow: false,
    };
  },
  created: function() {
    if (document.body.clientWidth <= 992) {
      this.mobile = true;
    }
    console.log(this.data);
  },
  methods: {
    goURL(url) {
      window.location = url;
    },
  },
};
</script>

